
body{
  background-color: #302f2d;
  height: 100vh;
  min-height: 100vh;
  display: grid;
  place-items: center;
}
.board-row {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
}

.board-container {
  margin: 20px;
  text-align: center;
  padding: 30px;
}
.btn{
  background-color: white;
  border: 1px solid black;
  border-radius: 5px;
  width: 50ex;
  height: 30px;
}
.winner{
  font-family: Verdana, Geneva, Tahoma, sans-serif;
  font-weight: 200;
  font-size:xx-large;
  text-transform: uppercase;

}
.winner-sign{
  color: red;
  font-weight: bold;
  margin-top: 10px;
}
